 <template>
    <div>
        <el-row>
            <el-col :span="7" class="mypos-order" id="mypos-list">
                <el-tabs>
                    <el-tab-pane label="点餐">
                        <el-table :data="tableData" border style="width: 100%">
                            <el-table-column prop="goodsName" label="商品名称"></el-table-column>
                            <el-table-column prop="count" width="50" label="数量"></el-table-column>
                            <el-table-column prop="price" width="70" label="金额"></el-table-column>
                            <el-table-column width="150" fixed="right" label="操作">
                                <template scope="scope">
                                    <el-button type="text" size="small" @click="reduceOrderList(scope.row)">减少</el-button>
                                    <el-button type="text" size="small" @click="addOrderList(scope.row)">增加</el-button>
                                    <el-button type="text" size="small" @click="delSingleGoods(scope.row)">删除</el-button>
                                </template>

                            </el-table-column>
                        </el-table>
                        <div class="totalDiv">
                            <small>数量：</small>{{totalCount}}
                            <small>合计：</small>{{totalMoney}}

                        </div>
                        <div class="div-btn">
                            <el-button type="warning" @click="unchecked()">挂单</el-button>
                            <el-button type="danger" @click="delAllGoods()">删除</el-button>
                            <el-button type="success" @click="checkout()">结账</el-button>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="挂单">
                        <el-table :data="uncheckedData" border style="width: 100%">
                            <el-table-column prop="goodsName" label="商品名称"></el-table-column>
                            <el-table-column prop="count" width="50" label="数量"></el-table-column>
                            <el-table-column prop="price" width="70" label="金额"></el-table-column>
                        </el-table>
                        <div class="totalDiv">
                            <small>数量：</small>{{totalCount_u}}
                            <small>合计：</small>{{totalMoney_u}}

                        </div>
                        <div class="div-btn">
                            <el-button type="danger" @click="delAllGoods_u()">删除</el-button>
                            <el-button type="success" @click="checkout_u()">结账</el-button>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="外卖">外卖</el-tab-pane>
                </el-tabs>
            </el-col>
            <el-col :span="17">
                <div class="often-goods">
                    <div class="title">常用商品</div>
                    <div class="often-goods-list">
                        <ul>
                            <li v-for="item in oftenGooods" @click="addOrderList(item)">
                                <span>{{item.goodsName}}</span>
                                <span>￥{{item.price}}</span>
                            </li>
                        </ul>
                    </div>
                </div>

                <div class="goods-type">
                    <el-tabs>
                        <el-tab-pane label="主食">
                            <div>
                                <div class="cookList">
                                    <li v-for="item in type0Goods" @click="addOrderList(item)">
                                        <span class="foodImg"><img :src="item.goodsImg" alt=""></span>
                                        <div class="name_price">
                                            <span class="foodName">{{item.goodsName}}</span>
                                            <span class="foodPrice">￥{{item.price}}</span>
                                        </div>
                                    </li>
                                </div>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="小吃">
                            <div>
                                <div class="cookList">
                                    <li v-for="item in type1Goods" @click="addOrderList(item)">
                                        <span class="foodImg"><img :src="item.goodsImg" alt=""></span>
                                        <div class="name_price">
                                            <span class="foodName">{{item.goodsName}}</span>
                                            <span class="foodPrice">￥{{item.price}}</span>
                                        </div>
                                    </li>
                                </div>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="饮料">
                            <div>
                                <div class="cookList">
                                    <li v-for="item in type2Goods" @click="addOrderList(item)">
                                        <span class="foodImg"><img :src="item.goodsImg" alt=""></span>
                                        <div class="name_price">
                                            <span class="foodName">{{item.goodsName}}</span>
                                            <span class="foodPrice">￥{{item.price}}</span>
                                        </div>
                                    </li>
                                </div>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="套餐">
                            <div>
                                <div class="cookList">
                                    <li v-for="item in type3Goods" @click="addOrderList(item)">
                                        <span class="foodImg"><img :src="item.goodsImg" alt=""></span>
                                        <div class="name_price">
                                            <span class="foodName">{{item.goodsName}}</span>
                                            <span class="foodPrice">￥{{item.price}}</span>
                                        </div>
                                    </li>
                                </div>
                            </div>
                        </el-tab-pane>

                    </el-tabs>

                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      totalCount: 0,
      totalMoney: 0,
      totalCount_u: 0,
      totalMoney_u: 0,
      tableData: [],
      oftenGooods: [
        {
          goodsId: 1,
          goodsName: "香辣鸡腿堡",
          price: 18
        },
        {
          goodsId: 2,
          goodsName: "田园鸡腿堡",
          price: 15
        },
        {
          goodsId: 3,
          goodsName: "和风汉堡",
          price: 15
        },
        {
          goodsId: 4,
          goodsName: "快乐全家桶",
          price: 80
        },
        {
          goodsId: 5,
          goodsName: "脆皮炸鸡腿",
          price: 10
        },
        {
          goodsId: 6,
          goodsName: "魔法鸡块",
          price: 20
        },
        {
          goodsId: 7,
          goodsName: "可乐大杯",
          price: 10
        },
        {
          goodsId: 8,
          goodsName: "雪顶咖啡",
          price: 18
        },
        {
          goodsId: 9,
          goodsName: "大块鸡米花",
          price: 15
        },
        {
          goodsId: 20,
          goodsName: "香脆鸡柳",
          price: 17
        }
      ],
      type0Goods: [
        {
          goodsId: 1,
          goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos001.jpg",
          goodsName: "香辣鸡腿堡",
          price: 18
        },
        {
          goodsId: 2,
          goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg",
          goodsName: "田园鸡腿堡",
          price: 15
        },
        {
          goodsId: 3,
          goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos004.jpg",
          goodsName: "和风汉堡",
          price: 15
        },
        {
          goodsId: 4,
          goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg",
          goodsName: "快乐全家桶",
          price: 80
        },
        {
          goodsId: 5,
          goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg",
          goodsName: "脆皮炸鸡腿",
          price: 10
        },
        {
          goodsId: 6,
          goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos004.jpg",
          goodsName: "魔法鸡块",
          price: 20
        },
        {
          goodsId: 7,
          goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos001.jpg",
          goodsName: "可乐大杯",
          price: 10
        },
        {
          goodsId: 8,
          goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg",
          goodsName: "雪顶咖啡",
          price: 18
        },
        {
          goodsId: 9,
          goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg",
          goodsName: "大块鸡米花",
          price: 15
        },
        {
          goodsId: 20,
          goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg",
          goodsName: "香脆鸡柳",
          price: 17
        }
      ],
      type1Goods: [],
      type2Goods: [],
      type3Goods: [],
      uncheckedData: []
    };
  },
  mounted: function() {
    var orderHeight = document.body.clientHeight;
    document.getElementById("mypos-list").style.height = orderHeight + "px";
  },
  created: function() {
    // axios
    //   .get(
    //     "https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/oftenGoods"
    //   )
    //   .then(response => {
    //     this.oftenGooods = response.data;
    //   })
    //   .catch(error => {
    //     alert("网络错误，不能访问！");
    //   });

    // axios
    //   .get(
    //     "https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/typeGoods"
    //   )
    //   .then(response => {
    //     this.type0Goods = response.data[0];
    //     this.type1Goods = response.data[1];
    //     this.type2Goods = response.data[2];
    //     this.type3Goods = response.data[3];
    //   })
    //   .catch(error => {
    //     alert("网络错误，不能访问！");
    //   });
  },
  methods: {
    addOrderList: function(goods) {
      this.totalCount = 0;
      this.totalMoney = 0;
      let isHave = false;
      for (let i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].goodsId == goods.goodsId) {
          isHave = true;
        }
      }
      if (isHave) {
        let arr = this.tableData.filter(o => o.goodsId == goods.goodsId);
        arr[0].count++;
      } else {
        let newGoods = {
          goodsId: goods.goodsId,
          goodsName: goods.goodsName,
          price: goods.price,
          count: 1
        };
        this.tableData.push(newGoods);
      }
      this.summary();
    },
    reduceOrderList: function(goods) {
      let arr = this.tableData.filter(o => o.goodsId == goods.goodsId);
      arr[0].count == 1
        ? (this.tableData = this.tableData.filter(
            o => o.goodsId != goods.goodsId
          ))
        : arr[0].count--;
      this.summary();
    },
    delSingleGoods(goods) {
      this.tableData = this.tableData.filter(o => o.goodsId != goods.goodsId);
      this.summary();
    },
    delAllGoods() {
      this.tableData = [];
      this.totalCount = 0;
      this.totalMoney = 0;
    },
    delAllGoods_u() {
      this.uncheckedData = [];
      this.totalCount_u = 0;
      this.totalMoney_u = 0;
    },
    checkout() {
      if (this.totalCount != 0) {
        this.tableData = [];
        this.totalCount = 0;
        this.totalMoney = 0;
        this.$message({
          message: "结账成功，感谢您又为小店的成长做出了一份贡献！",
          type: "success"
        });
      } else {
        this.$message.error("客户还没有点餐，请先指引客户点餐再结账");
      }
    },
    checkout_u() {
      if (this.uncheckedData != 0) {
        this.uncheckedData = [];
        this.totalCount_u = 0;
        this.totalMoney_u = 0;
        this.$message({
          message: "结账成功，感谢您又为小店的成长做出了一份贡献！",
          type: "success"
        });
      } else {
        this.$message.error("客户还没有点餐，请先指引客户点餐再结账");
      }
    },
    // 挂单
    unchecked() {
      if (this.totalCount != 0) {
        this.uncheckedData = this.tableData;
        this.totalCount_u = this.totalCount;
        this.totalMoney_u = this.totalMoney;
        this.tableData = [];
        this.totalCount = 0;
        this.totalMoney = 0;
        this.$message({
          message: "挂单成功，待顾客用餐后记得提醒顾客买单！",
          type: "success"
        });
      } else {
        this.$message.error("客户还没有点餐，请先指引客户点餐再结账");
      }
    },
    summary() {
      this.totalCount = 0;
      this.totalMoney = 0;
      if (this.tableData) {
        this.tableData.forEach(element => {
          this.totalCount += element.count;
          this.totalMoney = this.totalMoney + element.price * element.count;
        });
      }
    }
  }
};
</script>

<style>
.mypos-order {
  background: #f9fafc;
  border-right: 1px solid #c0ccda;
}

.div-btn {
  margin: 50px;
}

.totalDiv {
  background: #fff;
  padding: 10px;
  border-bottom: 1px solid #d3dce6;
}

.title {
  text-align: left;
  height: 40px;
  line-height: 40px;
}

.often-goods-list ul {
  overflow: hidden;
  font-family: "微软雅黑";
}

.often-goods-list li {
  list-style: none;
  float: left;
  padding: 10px;
  margin: 10px;
  background: #fff;
  border: 1px solid #e5e9f2;
  cursor: pointer;
}

.cookList {
  overflow: hidden;
}

.cookList li {
  list-style: none;
  float: left;
  padding: 10px;
  margin: 10px;
  width: 23%;
  border: 1px solid #e5e9f2;
  height: auto;
  background: #fff;
  cursor: pointer;
}

.cookList li<span {
  display: block;
  float: left;
}

.cookList .name_price {
  display: block;
  float: left;
  width: 60%;
}

.foodImg {
  width: 40%;
}

.foodImg img {
  width: 100%;
  height: 100%;
}

.foodName {
  font-size: 18px;
  padding-left: 10px;
  color: brown;
}

.foodPrice {
  font-size: 16px;
  padding-left: 10px;
  padding-top: 10px;
  display: block;
}
</style>


